<script>
export default {
  methods: {
    enterDashboard(adminView) {
      const vm = this;
      vm.$store.dispatch('loadingOverlay');
      localStorage.setItem(
        'adminView',
        JSON.stringify({ adminView: adminView })
      );
      setTimeout(() => {
        vm.$store.dispatch('loadingOverlay');
        vm.$router.push({ path: 'dashboard' });
      }, 750);
    },
  },
